.at-avatar {
  width: 2.13333rem;
  height: 2.13333rem;
  color: #fff;
  font-size: 0.85333rem;
  line-height: 2.13333rem;
  text-align: center;
  border-radius: 0.17067rem;
  background: #E5E5E5;
  box-shadow: 0 0 1.06667rem 0 rgba(0, 0, 0, 0.05);
  overflow: hidden;
}

.at-avatar__img {
  width: 100%;
  height: 100%;
}

.at-avatar--large {
  width: 2.56rem;
  height: 2.56rem;
  font-size: 1.024rem;
  line-height: 2.56rem;
}

.at-avatar--small {
  width: 1.70667rem;
  height: 1.70667rem;
  font-size: 0.68267rem;
  line-height: 1.70667rem;
}

.at-avatar--circle {
  border-radius: 50%;
  background-clip: border-box;
  overflow: hidden;
}

